/* End Flex style */


.currency-wrapper {
  float: left;
  width: 100%;
  background-color: rgb(214 235 255);
  height: 100vh;

}
.nav-wrap{float: left; width: 100%; background-color: #000000; padding: 20px 0; position: fixed; top: 0; left: 0;}
.nav-wrap .common-wrap{width: 100%;}
.logo {float: left; width: 200px;}
.logo a{width: 100%;}
.nav-wrap ul{float: left; width: calc(100% - 200px); display: flex; flex-flow: row wrap; justify-content: flex-end}
.nav-wrap ul li{margin-left: 30px;}
.nav-wrap ul li a{color: #ffffff;}

.main-content-wrap{float: left; width: 100%; padding: 90px 0 50px;display: flex;
  flex-flow: row wrap;
  align-items: center;
  height: calc(100% - 83px);}
.intro-heading{float: left; width: 100%; text-align: center; }
.App{float: left; width: 100%; padding: 50px 0 10px;} 
.main-content-wrap .common-wrap{max-width: 700px;}
.groups{
  background: #335;
  width: 280px;
  margin: 0 auto 20px;
  display: grid;
  grid-template-columns: 200px 80px;
  border-radius: 15px;
}
input,select{
  background: none;
  border:0;
  color:#fff;
}
input{
  padding-left: 20px;
}
select{
  padding: 15px;
}
select option{
  color: #000000;
  min-height: 20px;
  font-size: 16px;
  line-height: 26px;
}
.bottom-nav {
  float: left;
  width: 100%;
  padding: 10px 0;
  position: absolute;
  bottom: 0;
  left: 0;
}
.bottom-nav ul{ padding: 0 30px;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;}

.bottom-nav ul li{font-size: 16px; line-height: 26px;}
.bottom-nav ul li a{color: #00860b; text-transform: uppercase;}

.main-content-wrap p{font-size: 16px; line-height: 26px; text-align: center; }







@media only screen and (min-width: 280px) and (max-width: 767px) {
  .main-content-wrap, .currency-wrapper{height: 100%;}
  .bottom-nav {
    float: left;
    width: 100%;
    padding: 10px 0;
    position: relative;
    bottom: 0;
    left: 0;
  }
  .bottom-nav ul{padding: 0 15px;}
  .bottom-nav ul li{font-size: 14px;}
  .mobi-nav{width: 30px; height: 20px;}

  .nav-wrap ul {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    height: 100vh;
    background: #000000;
    padding: 100px 0;
    text-align: center;
    transform: translateX(102%);
    display: block;
    transition: 0.3s ease;
}

.navShown .nav-wrap ul {
  transform: translateX(0);
}

.nav-wrap ul li{margin-left: 0; margin-bottom: 30px;}
.logo {width: 100%; position: relative; z-index: 9;}
.logo a {width: 150px;}
.mobi-nav{position: absolute; right: 0; top: 50%; transform: translateY(-50%);}
.mobi-nav div {
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.mobi-nav div::before, .mobi-nav div::after{content: ''; width: 100%; height: 1px; background-color: #ffffff; position: absolute; top: -10px; left: 0; transition: 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);}
.mobi-nav div::after{top: auto; bottom: -10px;}

.navShown .mobi-nav div::before{
  content: '';
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  top: 0px;
  left: 0;
  transform: rotate(
90deg);
}

.navShown .mobi-nav div {transform: rotate(-45deg);}
.navShown .mobi-nav div::after {opacity: 0;}






}

